(function() {
  this.App.component('mnoeStaffsList', {
    templateUrl: 'app/components/mnoe-staffs-list/mnoe-staffs-list.html',
    bindings: {
      view: '@'
    },
    controller: ["$filter", "$log", "MnoeUsers", "MnoeCurrentUser", "MnoConfirm", "MnoeObservables", "ADMIN_ROLES", "OBS_KEYS", "toastr", function($filter, $log, MnoeUsers, MnoeCurrentUser, MnoConfirm, MnoeObservables, ADMIN_ROLES, OBS_KEYS, toastr) {
      var fetchStaffs, onStaffAdded, onStaffChanged, updateSearch, updateSort, vm;
      vm = this;
      vm.listOfStaff = [];
      vm.callServer = function(tableState) {
        var search, sort;
        sort = updateSort(tableState.sort);
        search = updateSearch(tableState.search);
        return fetchStaffs(vm.staff.nbItems, vm.staff.offset, sort, search);
      };
      updateSort = function(sortState) {
        var sort;
        if (sortState == null) {
          sortState = {};
        }
        sort = "surname";
        if (sortState.predicate) {
          sort = sortState.predicate;
          if (sortState.reverse) {
            sort += ".desc";
          } else {
            sort += ".asc";
          }
        }
        vm.staff.sort = sort;
        return sort;
      };
      updateSearch = function(searchingState) {
        var attr, ref, search, value;
        if (searchingState == null) {
          searchingState = {};
        }
        search = {};
        if (searchingState.predicateObject) {
          ref = searchingState.predicateObject;
          for (attr in ref) {
            value = ref[attr];
            if (attr === "admin_role") {
              search['where[admin_role.in][]'] = [value];
            } else {
              search['where[' + attr + '.like]'] = value + '%';
            }
          }
        }
        vm.staff.search = search;
        return search;
      };
      vm.state = vm.view;
      vm.getAdminRoleLabel = function(admin_role) {
        return _.find(vm.staff.roles, function(role) {
          return role.value === admin_role;
        }).label;
      };
      vm.staff = {
        editmode: [],
        search: {},
        sort: "surname",
        nbItems: 10,
        page: 1,
        roles: ADMIN_ROLES,
        pageChangedCb: function(nbItems, page) {
          var offset;
          vm.staff.nbItems = nbItems;
          vm.staff.page = page;
          offset = (page - 1) * nbItems;
          return fetchStaffs(nbItems, offset);
        },
        update: function(staff) {
          return MnoeUsers.updateStaff(staff).then(function(response) {
            updateSort();
            updateSearch();
            delete vm.staff.editmode[staff.id];
            return MnoeCurrentUser.refreshUser();
          }, function(error) {
            $log.error('Error while saving user', error);
            return toastr.error('mnoe_admin_panel.dashboard.staff.widget.list.toastr_error');
          });
        },
        remove: function(staff) {
          var modalOptions;
          modalOptions = {
            closeButtonText: 'mnoe_admin_panel.dashboard.staff.modal.remove_staff.cancel',
            actionButtonText: 'mnoe_admin_panel.dashboard.staff.modal.remove_staff.delete',
            headerText: 'mnoe_admin_panel.dashboard.staff.modal.remove_staff.proceed',
            headerTextExtraData: {
              staff_name: staff.name + " " + staff.surname
            },
            bodyText: 'mnoe_admin_panel.dashboard.staff.modal.remove_staff.perform'
          };
          return MnoConfirm.showModal(modalOptions).then(function() {
            return MnoeUsers.removeStaff(staff.id).then(function() {
              return toastr.success('mnoe_admin_panel.dashboard.staff.widget.list.toastr_success', {
                extraData: {
                  staff_name: staff.name + " " + staff.surname
                }
              });
            });
          });
        }
      };
      fetchStaffs = function(limit, offset, sort, search) {
        if (sort == null) {
          sort = vm.staff.sort;
        }
        if (search == null) {
          search = vm.staff.search;
        }
        vm.staff.loading = true;
        return MnoeUsers.staffs(limit, offset, sort, search).then(function(response) {
          vm.staff.totalItems = response.headers('x-total-count');
          vm.listOfStaff = response.data;
          return vm.staff.oneAdminLeft = _.filter(response.data, {
            'admin_role': 'admin'
          }).length === 1;
        })["finally"](function() {
          return vm.staff.loading = false;
        });
      };
      fetchStaffs(vm.staff.nbItems, 0).then(function() {
        MnoeObservables.registerCb(OBS_KEYS.staffAdded, function() {
          return fetchStaffs(vm.staff.nbItems, vm.staff.offset);
        });
        return MnoeObservables.registerCb(OBS_KEYS.staffChanged, function() {
          return fetchStaffs(vm.staff.nbItems, vm.staff.offset);
        });
      });
      onStaffAdded = function() {
        return fetchStaffs(vm.staff.nbItems, vm.staff.offset);
      };
      onStaffChanged = function() {
        return fetchStaffs(vm.staff.nbItems, vm.staff.offset);
      };
      MnoeObservables.registerCb(OBS_KEYS.staffAdded, onStaffAdded);
      MnoeObservables.registerCb(OBS_KEYS.staffChanged, onStaffChanged);
      this.$onDestroy = function() {
        MnoeObservables.unsubscribe(OBS_KEYS.staffAdded, onStaffAdded);
        return MnoeObservables.unsubscribe(OBS_KEYS.staffChanged, onStaffChanged);
      };
    }]
  });

}).call(this);
