(function() {
  this.App.service('MnoeOrganizations', ["MnoeAdminApiSvc", function(MnoeAdminApiSvc) {
    var _self, notifyListObservers, observerCallbacks;
    _self = this;
    this.list = function(limit, offset, sort) {
      var promise;
      return promise = MnoeAdminApiSvc.all("organizations").getList({
        order_by: sort,
        limit: limit,
        offset: offset
      }).then(function(response) {
        notifyListObservers(promise);
        return response;
      });
    };
    observerCallbacks = [];
    this.registerListChangeCb = function(callback) {
      return observerCallbacks.push(callback);
    };
    notifyListObservers = function(listPromise) {
      return _.forEach(observerCallbacks, function(callback) {
        return callback(listPromise);
      });
    };
    this.search = function(terms) {
      return MnoeAdminApiSvc.all("organizations").getList({
        terms: terms
      });
    };
    this.inArrears = function() {
      return MnoeAdminApiSvc.all('organizations').all('in_arrears').getList();
    };
    this.get = function(id) {
      return MnoeAdminApiSvc.one('organizations', id).get();
    };
    this.count = function() {
      return MnoeAdminApiSvc.all('organizations').customGET('count');
    };
    this.create = function(organization) {
      return MnoeAdminApiSvc.all('/organizations').post(organization);
    };
    this.update = function(organization) {
      return MnoeAdminApiSvc.one('/organizations', organization.id).patch(organization);
    };
    return this;
  }]);

}).call(this);
