(function() {
  angular.module('frontendAdmin').controller('QuestionReplyModal', ["$scope", "$uibModalInstance", function($scope, $uibModalInstance) {
    $scope.replyText = '';
    $scope.closeModal = function() {
      return $uibModalInstance.dismiss();
    };
    $scope.submitIteraction = function() {
      return $uibModalInstance.close($scope.replyText);
    };
  }]);

}).call(this);
