(function() {
  this.App.directive('mnoeUsersList', ["$filter", "$log", "$translate", "MnoeUsers", function($filter, $log, $translate, MnoeUsers) {
    return {
      restrict: 'E',
      scope: {},
      templateUrl: 'app/components/mnoe-users-list/mno-users-list.html',
      link: function(scope, elem, attrs) {
        var displayCurrentState, fetchUsers, setAllUsersList, setLastUsersList, setSearchUsersList;
        scope.state = attrs.view;
        scope.users = {
          search: '',
          nbItems: 10,
          page: 1,
          pageChangedCb: function(nbItems, page) {
            var offset;
            scope.users.nbItems = nbItems;
            scope.users.page = page;
            offset = (page - 1) * nbItems;
            return fetchUsers(nbItems, offset);
          }
        };
        fetchUsers = function(limit, offset, sort) {
          if (sort == null) {
            sort = 'surname';
          }
          scope.users.loading = true;
          return MnoeUsers.list(limit, offset, sort).then(function(response) {
            scope.users.totalItems = response.headers('x-total-count');
            return scope.users.list = response.data;
          })["finally"](function() {
            return scope.users.loading = false;
          });
        };
        scope.switchState = function() {
          scope.state = attrs.view = attrs.view === 'all' ? 'last' : 'all';
          return displayCurrentState();
        };
        displayCurrentState = function() {
          if (attrs.view === 'all') {
            setAllUsersList();
            return fetchUsers(scope.users.nbItems, 0);
          } else if (attrs.view === 'last') {
            setLastUsersList();
            return fetchUsers(10, 0, 'created_at.desc');
          } else {
            return $log.error('Value of attribute view can only be "all" or "last"');
          }
        };
        setAllUsersList = function() {
          scope.users.widgetTitle = 'mnoe_admin_panel.dashboard.users.widget.list.all_users.title';
          return scope.users.switchLinkTitle = 'mnoe_admin_panel.dashboard.users.widget.list.all_users.switch_link_title';
        };
        setLastUsersList = function() {
          scope.users.widgetTitle = 'mnoe_admin_panel.dashboard.users.widget.list.last_users.title';
          return scope.users.switchLinkTitle = 'mnoe_admin_panel.dashboard.users.widget.list.last_users.switch_link_title';
        };
        scope.searchChange = function() {
          if (scope.users.search.length >= 3) {
            scope.searchMode = true;
            return setSearchUsersList(scope.users.search);
          } else if (scope.searchMode) {
            scope.searchMode = false;
            return displayCurrentState();
          }
        };
        setSearchUsersList = function(search) {
          var terms;
          scope.users.loading = true;
          scope.users.widgetTitle = 'mnoe_admin_panel.dashboard.users.widget.list.search_users.title';
          delete scope.users.switchLinkTitle;
          search = scope.users.search.toLowerCase();
          terms = {
            'surname.like': search + "%",
            'name.like': search + "%",
            'email.like': "%" + search + "%"
          };
          return MnoeUsers.search(terms).then(function(response) {
            scope.users.totalItems = response.headers('x-total-count');
            return scope.users.list = $filter('orderBy')(response.data, 'email');
          })["finally"](function() {
            return scope.users.loading = false;
          });
        };
        return displayCurrentState();
      }
    };
  }]);

}).call(this);
