(function() {
  this.App.controller('CustomersController', ["$scope", "$uibModal", "MnoeUsers", "MnoeOrganizations", "MnoeObservables", "OBS_KEYS", function($scope, $uibModal, MnoeUsers, MnoeOrganizations, MnoeObservables, OBS_KEYS) {
    'ngInject';
    var updateUsersCounter, vm;
    vm = this;
    vm.users = {};
    vm.organizations = {};
    vm.invoices = {};
    vm.inviteUserModal = function() {
      return $uibModal.open({
        templateUrl: 'app/views/customers/invite-user-modal/invite-user.html',
        controller: 'InviteUserController',
        controllerAs: 'vm'
      });
    };
    updateUsersCounter = function(response) {
      vm.users.totalCount = response.headers('x-total-count');
    };
    MnoeObservables.registerCb(OBS_KEYS.userChanged, updateUsersCounter);
    MnoeOrganizations.registerListChangeCb(function(promise) {
      return promise.then(function(response) {
        return vm.organizations.totalCount = response.headers('x-total-count');
      });
    });
    $scope.$on('$destroy', function() {
      return MnoeObservables.unsubscribe(OBS_KEYS.userChanged, updateUsersCounter);
    });
  }]);

}).call(this);
