(function() {
  angular.module('frontendAdmin').config(["ImpacRoutesProvider", "IMPAC_CONFIG", function(ImpacRoutesProvider, IMPAC_CONFIG) {
    var data, mnoHub;
    mnoHub = IMPAC_CONFIG.paths.mnohub_api;
    data = {
      mnoHub: mnoHub,
      impacApi: IMPAC_CONFIG.protocol + "://" + IMPAC_CONFIG.host + "/api",
      dashboards: {
        index: mnoHub + "/admin/impac/dashboard_templates",
        show: mnoHub + "/admin/impac/dashboard_templates/:id"
      },
      widgets: {
        update: mnoHub + "/admin/impac/widgets/:id",
        del: mnoHub + "/admin/impac/widgets/:id"
      },
      kpis: {
        index: mnoHub + "/impac/kpis",
        update: mnoHub + "/admin/impac/kpis/:id",
        del: mnoHub + "/admin/impac/kpis/:id"
      }
    };
    return ImpacRoutesProvider.configureRoutes(data);
  }]).config(["ImpacThemingProvider", function(ImpacThemingProvider) {
    var options;
    options = {
      dataNotFoundConfig: {
        linkUrl: '#!/marketplace',
        linkTarget: '_self'
      },
      dhbConfig: {
        designerMode: {
          enabled: true,
          dhbLabelName: 'Template'
        }
      },
      dhbErrorsConfig: {
        firstTimeCreated: {
          note: ''
        }
      },
      dhbSelectorConfig: {
        pdfModeEnabled: false,
        addDhbEnabled: false,
        deleteDhbEnabled: false,
        selectorType: 'dropdown'
      },
      dhbSettings: {
        syncApps: {
          show: function() {
            return false;
          }
        }
      },
      dhbKpisConfig: {
        enableKpis: true
      },
      alertsConfig: {
        enableAlerts: false
      }
    };
    return ImpacThemingProvider.configure(options);
  }]).config(["ImpacAssetsProvider", function(ImpacAssetsProvider) {
    var options;
    options = {
      defaultImagesPath: '/dashboard/images'
    };
    return ImpacAssetsProvider.configure(options);
  }]).run(["ImpacLinking", "ImpacConfigSvc", "IMPAC_CONFIG", function(ImpacLinking, ImpacConfigSvc, IMPAC_CONFIG) {
    var data;
    data = {
      user: ImpacConfigSvc.getUserData,
      organizations: ImpacConfigSvc.getOrganizations
    };
    if ((IMPAC_CONFIG.pusher != null) && (IMPAC_CONFIG.pusher.key != null)) {
      data.pusher_key = IMPAC_CONFIG.pusher.key;
    }
    return ImpacLinking.linkData(data);
  }]);

}).call(this);
