(function() {
  this.App.constant('ITEMS_PER_PAGE', [10, 20, 50, 100]);

  this.App.directive('mnoPagination', ["ITEMS_PER_PAGE", function(ITEMS_PER_PAGE) {
    return {
      restrict: 'AE',
      scope: {
        totalItems: '=',
        onChangeCb: '&',
        nbItemsValues: '=?',
        nbItems: '=',
        page: '=',
        isLoading: '=?'
      },
      templateUrl: 'app/components/mno-pagination/mno-pagination.html',
      link: function(scope) {
        scope.pagination = {
          maxPages: 7
        };
        if (!scope.nbItemsValues) {
          scope.nbItemsValues = ITEMS_PER_PAGE;
        }
        return scope.onPageChange = function() {
          return scope.onChangeCb({
            nbItems: scope.nbItems,
            page: scope.page
          });
        };
      }
    };
  }]);

}).call(this);
