(function() {
  this.App.controller('InviteUserController', ["$filter", "$stateParams", "$uibModalInstance", "toastr", "MnoeUsers", "MnoErrorsHandler", "USER_ROLES", function($filter, $stateParams, $uibModalInstance, toastr, MnoeUsers, MnoErrorsHandler, USER_ROLES) {
    'ngInject';
    var vm;
    vm = this;
    vm.USER_ROLES = USER_ROLES;
    vm.onSubmit = function() {
      vm.isLoading = true;
      return MnoeUsers.sendSignupEmail(vm.user.email).then(function(success) {
        toastr.success('mnoe_admin_panel.dashboard.customers.invite_user_modal.toastr_success', {
          extraData: {
            email: vm.user.email
          }
        });
        return $uibModalInstance.close(vm.user.email);
      }, function(error) {
        toastr.error('mnoe_admin_panel.dashboard.customers.invite_user_modal.toastr_error', {
          extraData: {
            email: vm.user.email
          }
        });
        return MnoErrorsHandler.processServerError(error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
  }]);

}).call(this);
