(function() {
  this.App.controller('ConnectXeroModalCtrl', ["$window", "$httpParamSerializer", "$uibModalInstance", "MnoAppsInstances", "app", function($window, $httpParamSerializer, $uibModalInstance, MnoAppsInstances, app) {
    'ngInject';
    var vm;
    vm = this;
    vm.app = app;
    vm.form = {
      perform: true,
      "xero_country": "AU"
    };
    vm.countries = [
      {
        label: "mnoe_admin_panel.constants.countries.australia",
        value: "AU"
      }, {
        label: "mnoe_admin_panel.constants.countries.usa",
        value: "US"
      }
    ];
    vm.connect = function(form) {
      if (vm.payroll) {
        form['extra_params[]'] = "payroll";
      }
      return $window.location.href = MnoAppsInstances.oAuthConnectPath(app, $httpParamSerializer(form));
    };
    vm.close = function() {
      return $uibModalInstance.close();
    };
  }]);

}).call(this);
