(function() {
  this.App.controller('RemoveAppModalCtrl', ["$scope", "$uibModalInstance", "MnoeAppInstances", "app", function($scope, $uibModalInstance, MnoeAppInstances, app) {
    'ngInject';
    var vm;
    vm = this;
    vm.app = app;
    vm.modal = {
      loading: false
    };
    vm.deleteApp = function() {
      vm.modal.loading = true;
      return MnoeAppInstances.terminate(vm.app.id).then(function(success) {
        vm.errors = null;
        return $uibModalInstance.close(true);
      })["finally"](function() {
        return vm.modal.loading = false;
      });
    };
    vm.closeModal = function() {
      return $uibModalInstance.close(false);
    };
  }]);

}).call(this);
