(function() {
  this.App.factory('MnoeAdminConfig', ["$log", "ADMIN_PANEL_CONFIG", "PAYMENT_CONFIG", "ORGANIZATION_MANAGEMENT", function($log, ADMIN_PANEL_CONFIG, PAYMENT_CONFIG, ORGANIZATION_MANAGEMENT) {
    var _self;
    _self = this;
    this.isAuditLogEnabled = function() {
      if (ADMIN_PANEL_CONFIG.audit_log) {
        return ADMIN_PANEL_CONFIG.audit_log.enabled;
      } else {
        return true;
      }
    };
    this.isImpersonationEnabled = function() {
      if (ADMIN_PANEL_CONFIG.impersonation) {
        return !ADMIN_PANEL_CONFIG.impersonation.disabled;
      } else {
        return true;
      }
    };
    this.isStaffEnabled = function() {
      if (ADMIN_PANEL_CONFIG.staff != null) {
        return ADMIN_PANEL_CONFIG.staff.enabled;
      } else {
        return true;
      }
    };
    this.isFinanceEnabled = function() {
      if (ADMIN_PANEL_CONFIG.finance != null) {
        return ADMIN_PANEL_CONFIG.finance.enabled;
      } else {
        return true;
      }
    };
    this.isAppManagementEnabled = function() {
      if (ADMIN_PANEL_CONFIG.apps_management != null) {
        return ADMIN_PANEL_CONFIG.apps_management.enabled;
      } else {
        return true;
      }
    };
    this.isOrganizationManagementEnabled = function() {
      if ((ADMIN_PANEL_CONFIG.customer_management != null) && (ADMIN_PANEL_CONFIG.customer_management.organization != null)) {
        return ADMIN_PANEL_CONFIG.customer_management.organization.enabled;
      } else {
        return true;
      }
    };
    this.isUserManagementEnabled = function() {
      if ((ADMIN_PANEL_CONFIG.customer_management != null) && (ADMIN_PANEL_CONFIG.customer_management.user != null)) {
        return ADMIN_PANEL_CONFIG.customer_management.user.enabled;
      } else {
        return true;
      }
    };
    this.isPaymentEnabled = function() {
      var billing_disabled, payment_disabled;
      payment_disabled = (PAYMENT_CONFIG != null) && PAYMENT_CONFIG.disabled;
      billing_disabled = (ORGANIZATION_MANAGEMENT != null) && (ORGANIZATION_MANAGEMENT.billing != null) && !ORGANIZATION_MANAGEMENT.billing.enabled;
      return !(payment_disabled || billing_disabled);
    };
    this.isDashboardTemplatesEnabled = function() {
      return (ADMIN_PANEL_CONFIG.dashboard_templates != null) && ADMIN_PANEL_CONFIG.dashboard_templates.enabled;
    };
    return this;
  }]);

}).call(this);
