(function() {
  this.App.controller('CreateUserController', ["$filter", "$stateParams", "$uibModalInstance", "toastr", "MnoeUsers", "MnoErrorsHandler", "USER_ROLES", "organization", function($filter, $stateParams, $uibModalInstance, toastr, MnoeUsers, MnoErrorsHandler, USER_ROLES, organization) {
    'ngInject';
    var vm;
    vm = this;
    vm.USER_ROLES = USER_ROLES;
    vm.onSubmit = function() {
      vm.isLoading = true;
      return MnoeUsers.addUser(organization, vm.user).then(function(success) {
        toastr.success('mnoe_admin_panel.dashboard.organization.create_user.toastr_success', {
          extraData: {
            username: vm.user.name + " " + vm.user.surname
          }
        });
        return $uibModalInstance.close(success.data.user);
      }, function(error) {
        toastr.error('mnoe_admin_panel.dashboard.organization.create_user.toastr_error', {
          extraData: {
            username: vm.user.name + " " + vm.user.surname
          }
        });
        return MnoErrorsHandler.processServerError(error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
  }]);

}).call(this);
