(function() {
  this.App.service('MnoeDashboardTemplates', ["MnoeAdminApiSvc", "ImpacDashboardsSvc", function(MnoeAdminApiSvc, ImpacDashboardsSvc) {
    var _self, togglePublished;
    _self = this;
    this.list = function(limit, offset, sort) {
      return MnoeAdminApiSvc.all("/impac/dashboard_templates").getList({
        order_by: sort,
        limit: limit,
        offset: offset
      });
    };
    this.templates = function(limit, offset, sort, params) {
      if (params == null) {
        params = {};
      }
      params["order_by"] = sort;
      params["limit"] = limit;
      params["offset"] = offset;
      return MnoeAdminApiSvc.all("/impac/dashboard_templates").getList(params);
    };
    this["delete"] = function(dashboardTemplateId) {
      return MnoeAdminApiSvc.one("/impac/dashboard_templates", dashboardTemplateId).remove();
    };
    this.search = function(terms) {
      return MnoeAdminApiSvc.all("/impac/dashboard_templates").getList({
        terms: terms
      });
    };
    togglePublished = function(templateId) {
      var template;
      template = _.find(ImpacDashboardsSvc.getDashboards(), function(dhb) {
        return dhb.id === templateId;
      });
      if (template == null) {
        return;
      }
      return ImpacDashboardsSvc.update(templateId, {
        published: !template.published
      });
    };
    this.toggleTemplatePublished = function(templateId) {
      if (!angular.isDefined(ImpacDashboardsSvc.getDashboards())) {
        return togglePublished(templateId);
      }
      return ImpacDashboardsSvc.load().then(function() {
        return togglePublished(templateId);
      });
    };
    return this;
  }]);

}).call(this);
