(function() {
  this.App.controller('CreateStaffController', ["$filter", "$stateParams", "$log", "$uibModalInstance", "toastr", "MnoeUsers", "MnoErrorsHandler", "ADMIN_ROLES", function($filter, $stateParams, $log, $uibModalInstance, toastr, MnoeUsers, MnoErrorsHandler, ADMIN_ROLES) {
    'ngInject';
    var vm;
    vm = this;
    vm.admin_roles = ADMIN_ROLES;
    vm.onSubmit = function() {
      vm.isLoading = true;
      return MnoeUsers.addStaff(vm.user).then(function(success) {
        toastr.success("mnoe_admin_panel.dashboard.staff.add_staff.modal.toastr_success", {
          extraData: {
            username: vm.user.name + " " + vm.user.surname
          }
        });
        return $uibModalInstance.close(success.data);
      }, function(error) {
        toastr.error('mnoe_admin_panel.dashboard.staff.add_staff.modal.toastr_error', {
          extraData: {
            user_name: vm.user.name + " " + vm.user.surname
          }
        });
        return $log.error("An error occurred:", error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
  }]);

}).call(this);
