(function() {
  this.App.service('ImpacConfigSvc', ["$log", "$q", "MnoeCurrentUser", "MnoeOrganizations", function($log, $q, MnoeCurrentUser, MnoeOrganizations) {
    var _self;
    _self = this;
    this.getUserData = function() {
      return MnoeCurrentUser.getUser();
    };
    this.getOrganizations = function() {
      var userOrgsPromise;
      return userOrgsPromise = MnoeCurrentUser.getUser().then(function(user) {
        var err, firstOrgId, userOrgs;
        userOrgs = user.organizations;
        if (_.isEmpty(userOrgs)) {
          $log.error(err = {
            message: "Unable to retrieve user organizations"
          });
          return $q.reject(err);
        } else {
          firstOrgId = userOrgs[0].id;
          return $q.resolve({
            organizations: userOrgs,
            currentOrgId: firstOrgId
          });
        }
      });
    };
  }]);

}).call(this);
