(function() {
  this.App.controller('FinanceController', ["$filter", "MnoeInvoices", "MnoeTenantInvoices", "MnoeOrganizations", function($filter, MnoeInvoices, MnoeTenantInvoices, MnoeOrganizations) {
    'ngInject';
    var vm;
    vm = this;
    vm.invoices = {};
    vm.organizations = {};
    MnoeInvoices.currentBillingAmount().then(function(response) {
      return vm.invoices.currentBillingAmount = response.data;
    });
    MnoeInvoices.lastInvoicingAmount().then(function(response) {
      return vm.invoices.lastInvoicingAmount = response.data;
    });
    MnoeInvoices.lastPortfolioAmount().then(function(response) {
      return vm.invoices.lastPortfolioAmount = response.data;
    });
    MnoeInvoices.lastCommissionAmount().then(function(response) {
      return vm.invoices.lastCommissionAmount = response.data;
    });
    MnoeTenantInvoices.list().then(function(response) {
      vm.invoices.tenantInvoices = response.data;
      return vm.invoices.tenantInvoices = $filter('orderBy')(vm.invoices.tenantInvoices, '-started_at');
    });
    MnoeOrganizations.inArrears().then(function(response) {
      vm.organizations.inArrears = response.data;
      _.forEach(vm.organizations.inArrears, function(org) {
        return org.category = _.capitalize(org.category.replace("_", " "));
      });
      return vm.organizations.inArrears = $filter('orderBy')(vm.organizations.inArrears, '-started_at');
    });
  }]);

}).call(this);
