(function() {
  this.App.run(["$rootScope", "APP_NAME", function($rootScope, APP_NAME) {
    return $rootScope.app_name = APP_NAME;
  }]).run(["$rootScope", function($rootScope) {
    return $rootScope.$on('$viewContentLoaded', function() {
      return window.scrollTo(0, 0);
    });
  }]).run(["$templateCache", function($templateCache) {
    return $templateCache.put('directives/toast/toast.html', '<div class="{{toastClass}} {{toastType}}" ng-click="tapToast()">\n  <div ng-switch on="allowHtml">\n    <div ng-switch-default ng-if="title" class="{{titleClass}}" aria-label="{{title | translate}}">{{title | translate}}</div>\n    <div ng-switch-default class="{{messageClass}}" aria-label="{{message | translate:extraData}}">{{message | translate:extraData}}</div>\n    <div ng-switch-when="true" ng-if="title" class="{{titleClass}}" ng-bind-html="title"></div>\n    <div ng-switch-when="true" class="{{messageClass}}" ng-bind-html="message"></div>\n  </div>\n  <progress-bar ng-if="progressBar"></progress-bar>\n</div>');
  }]);

}).call(this);
