(function() {
  this.App.service('MnoeObservables', function() {
    var _self, observersCallbacks;
    _self = this;
    observersCallbacks = {};
    this.registerCb = function(name, cb) {
      if (_.isEmpty(observersCallbacks[name])) {
        observersCallbacks[name] = [];
      }
      return observersCallbacks[name].push(cb);
    };
    this.notifyObservers = function(name, object) {
      return _.forEach(observersCallbacks[name], function(callback) {
        return callback(object);
      });
    };
    this.unsubscribe = function(name, cb) {
      _.pull(observersCallbacks[name], cb);
      if (_.isEmpty(observersCallbacks[name])) {
        return delete observersCallbacks[name];
      }
    };
  });

}).call(this);
