(function() {
  this.App.service('MnoeAppInstances', ["MnoeAdminApiSvc", "toastr", function(MnoeAdminApiSvc, toastr) {
    var _self;
    _self = this;
    this.terminate = function(id) {
      return MnoeAdminApiSvc.one('app_instances', id).remove().then(function(response) {}, function(error) {
        return toastr.error('mnoe_admin_panel.dashboard.organization.remove_app.toastr_error');
      });
    };
    return this;
  }]);

}).call(this);
