(function() {
  this.App.directive('mnoeUsersLocalList', ["$window", "$filter", "$log", "toastr", "MnoeAdminConfig", "MnoeUsers", "MnoErrorsHandler", function($window, $filter, $log, toastr, MnoeAdminConfig, MnoeUsers, MnoErrorsHandler) {
    return {
      restrict: 'E',
      scope: {
        list: '=',
        organization: '='
      },
      templateUrl: 'app/components/mnoe-users-local-list/mnoe-users-local-list.html',
      link: function(scope, elem, attrs) {
        var displayNormalState, setAllUsersList, setLastUsersList, setSearchUsersList;
        scope.users = {
          displayList: [],
          widgetTitle: 'mnoe_admin_panel.dashboard.users.widget.local_list.loading_users.title',
          search: ''
        };
        scope.isImpersonationEnabled = MnoeAdminConfig.isImpersonationEnabled();
        setAllUsersList = function() {
          scope.users.widgetTitle = 'mnoe_admin_panel.dashboard.users.widget.local_list.all_users.title';
          scope.users.switchLinkTitle = 'mnoe_admin_panel.dashboard.users.widget.local_list.all_users.switch_link_title';
          return scope.users.displayList = $filter('orderBy')(scope.list, 'email');
        };
        setLastUsersList = function() {
          scope.users.widgetTitle = 'mnoe_admin_panel.dashboard.users.widget.local_list.last_users.title';
          scope.users.switchLinkTitle = 'mnoe_admin_panel.dashboard.users.widget.local_list.last_users.switch_link_title';
          scope.users.displayList = $filter('orderBy')(scope.list, '-created_at');
          return scope.users.displayList = $filter('limitTo')(scope.users.displayList, 10);
        };
        setSearchUsersList = function() {
          var searchToLowerCase;
          scope.users.widgetTitle = 'mnoe_admin_panel.dashboard.users.widget.local_list.search_users.title';
          delete scope.users.switchLinkTitle;
          searchToLowerCase = scope.users.search.toLowerCase();
          scope.users.displayList = _.filter(scope.list, function(user) {
            var email, name, surname;
            if (user.email) {
              email = _.contains(user.email.toLowerCase(), searchToLowerCase);
            }
            if (user.name) {
              name = _.contains(user.name.toLowerCase(), searchToLowerCase);
            }
            if (user.surname) {
              surname = _.contains(user.surname.toLowerCase(), searchToLowerCase);
            }
            return email || name || surname;
          });
          return scope.users.displayList = $filter('orderBy')(scope.users.displayList, 'email');
        };
        displayNormalState = function() {
          if (attrs.view === 'all') {
            return setAllUsersList();
          } else if (attrs.view === 'last') {
            return setLastUsersList();
          } else {
            return $log.error('Value of attribute view can only be "all" or "last"');
          }
        };
        scope.switchState = function() {
          if (attrs.view === 'all') {
            attrs.view = 'last';
          } else {
            attrs.view = 'all';
          }
          return displayNormalState();
        };
        scope.searchChange = function() {
          if (scope.users.search === '') {
            return displayNormalState();
          } else {
            return setSearchUsersList();
          }
        };
        scope.sendInvitation = function(user) {
          user.isSendingInvite = true;
          return MnoeUsers.inviteUser(scope.organization, user).then(function(response) {
            toastr.success('mnoe_admin_panel.dashboard.users.widget.local_list.toastr_success', {
              extraData: {
                username: user.name + " " + user.surname
              }
            });
            return user.status = response.data.user.status;
          }, function(error) {
            toastr.error('mnoe_admin_panel.dashboard.users.widget.local_list.toastr_error', {
              extraData: {
                username: user.name + " " + user.surname
              }
            });
            return MnoErrorsHandler.processServerError(error);
          })["finally"](function() {
            return user.isSendingInvite = false;
          });
        };
        scope.impersonateUser = function(user) {
          var redirect, url;
          if (user) {
            redirect = window.encodeURIComponent("" + location.pathname + location.hash);
            url = "/mnoe/impersonate/user/" + user.id + "?redirect_path=" + redirect;
            return $window.location.href = url;
          }
        };
        return scope.$watch('list', function(newVal) {
          if (newVal) {
            return displayNormalState();
          }
        }, true);
      }
    };
  }]);

}).call(this);
