(function() {
  this.App.service('MnoeReviews', ["MnoeAdminApiSvc", "$log", "toastr", function(MnoeAdminApiSvc, $log, toastr) {
    var _self;
    _self = this;
    this.list = function() {
      return MnoeAdminApiSvc.all('app_reviews').getList().then(function(response) {
        return response;
      }, function(error) {
        $log.error('Error while fetching reviews', error);
        return toastr.error('mnoe_admin_panel.dashboard.reviews_list.toastr_error');
      });
    };
    this.updateRating = function(review) {
      var promise;
      return promise = MnoeAdminApiSvc.one('app_reviews', review.id).patch({
        status: review.status
      }).then(function(response) {
        return response;
      }, function(error) {
        $log.error('Error while updating review', error);
        return toastr.error('mnoe_admin_panel.dashboard.feedback.toastr_error');
      });
    };
    this.updateDescription = function(review) {
      var promise;
      return promise = MnoeAdminApiSvc.one('app_reviews', review.id).patch({
        description: review.description
      }).then(function(response) {
        return response;
      }, function(error) {
        $log.error('Error while updating review', error);
        return toastr.error('mnoe_admin_panel.dashboard.feedback.toastr_error');
      });
    };
    this.replyQuestion = function(id, replyText) {
      var promise;
      return promise = MnoeAdminApiSvc.all("/app_answers").post({
        question_id: id,
        app_answer: {
          description: replyText
        }
      }).then(function(response) {
        return response;
      }, function(error) {
        $log.error('Error while updating review', error);
        return toastr.error('mnoe_admin_panel.dashboard.questions_reply_modal.toastr_error');
      });
    };
    this.replyFeedback = function(id, replyText) {
      var promise;
      return promise = MnoeAdminApiSvc.all("/app_comments").post({
        feedback_id: id,
        app_comment: {
          description: replyText
        }
      }).then(function(response) {
        return response;
      }, function(error) {
        $log.error('Error while updating review', error);
        return toastr.error('mnoe_admin_panel.dashboard.comment_edit.toastr_error');
      });
    };
    return this;
  }]);

}).call(this);
