(function() {
  this.App.controller('StaffController', ["$filter", "$stateParams", "$uibModal", "toastr", "MnoeUsers", "MnoAppsInstances", function($filter, $stateParams, $uibModal, toastr, MnoeUsers, MnoAppsInstances) {
    'ngInject';
    var vm;
    vm = this;
    vm.staff = {
      createModal: function() {
        var modalInstance;
        return modalInstance = $uibModal.open({
          templateUrl: 'app/views/staff/create-staff-modal/create-staff.html',
          controller: 'CreateStaffController',
          controllerAs: 'vm'
        });
      }
    };
    vm.displayed = [];
    vm.callServer = function(tableState) {
      console.log(tableState);
      return vm.isLoading = false;
    };
  }]);

}).call(this);
