(function() {
  this.App.service('MnoeMarketplace', ["$log", "MnoeApiSvc", function($log, MnoeApiSvc) {
    var _self, marketplaceApi, marketplacePromise;
    _self = this;
    marketplaceApi = MnoeApiSvc.oneUrl('/marketplace');
    marketplacePromise = null;
    this.getApps = function() {
      if (marketplacePromise != null) {
        return marketplacePromise;
      }
      return marketplacePromise = marketplaceApi.get();
    };
    return this;
  }]);

}).call(this);
