(function() {
  this.App.controller('HomeController', ["moment", "MnoeUsers", "MnoeOrganizations", "MnoeInvoices", "MnoeAdminConfig", function(moment, MnoeUsers, MnoeOrganizations, MnoeInvoices, MnoeAdminConfig) {
    'ngInject';
    var vm;
    vm = this;
    vm.users = {};
    vm.organizations = {};
    vm.invoices = {};
    if (MnoeAdminConfig.isPaymentEnabled()) {
      vm.organisationKpiLocale = 'mnoe_admin_panel.dashboard.home.kpi.organizations.finance_text';
    } else {
      vm.organisationKpiLocale = 'mnoe_admin_panel.dashboard.home.kpi.organizations.link_text';
    }
    MnoeUsers.count().then(function(response) {
      return vm.users.kpi = response.data;
    });
    MnoeOrganizations.count().then(function(response) {
      return vm.organizations.kpi = response.data;
    });
    if (MnoeAdminConfig.isFinanceEnabled()) {
      MnoeInvoices.lastInvoicingAmount().then(function(response) {
        return vm.invoices.lastInvoicingAmount = response.data;
      });
    }
    if (MnoeAdminConfig.isFinanceEnabled()) {
      MnoeInvoices.outstandingAmount().then(function(response) {
        return vm.invoices.outstandingAmount = response.data;
      });
    }
  }]);

}).call(this);
