(function() {
  this.App.directive('mnoeReviewsList', ["$filter", "$log", "$uibModal", "MnoeReviews", function($filter, $log, $uibModal, MnoeReviews) {
    return {
      restric: 'E',
      scope: {},
      templateUrl: 'app/components/mnoe-reviews-list/mnoe-reviews-list.html',
      link: function(scope) {
        var fetchReviews;
        scope.editmode = [];
        scope.listOfReviews = [];
        scope.statuses = [
          {
            value: 'approved',
            label: 'mnoe_admin_panel.dashboard.reviews_list.status_label.approved'
          }, {
            value: 'rejected',
            label: 'mnoe_admin_panel.dashboard.reviews_list.status_label.rejected'
          }
        ];
        scope.openCommentModal = function() {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-reviews-list/comment-modal.html',
            controller: 'CommentModal'
          });
        };
        scope.openEditModal = function(review) {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-reviews-list/comment-edit-modal.html',
            controller: 'CommentEditModal',
            resolve: {
              review: review
            }
          }).result.then(function(review) {
            return MnoeReviews.updateDescription(review).then(function(response) {});
          });
        };
        scope.openFeedbackReplyModal = function(review) {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-reviews-list/feedback-reply-modal.html',
            controller: 'FeedbackReplyModal'
          }).result.then(function(replyText) {
            return MnoeReviews.replyFeedback(review.id, replyText).then(function(response) {
              return scope.listOfReviews.unshift(response.data.app_comment);
            });
          });
        };
        scope.openQuestionReplyModal = function(review) {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-reviews-list/question-reply-modal.html',
            controller: 'QuestionReplyModal'
          }).result.then(function(replyText) {
            return MnoeReviews.replyQuestion(review.id, replyText).then(function(response) {
              return scope.listOfReviews.unshift(response.data.app_answer);
            });
          });
        };
        fetchReviews = function() {
          return MnoeReviews.list().then(function(response) {
            return scope.listOfReviews = response.data;
          });
        };
        scope.update = function(review) {
          return MnoeReviews.updateRating(review).then(function(response) {
            return delete scope.editmode[review.id];
          });
        };
        fetchReviews();
      }
    };
  }]);

}).call(this);
