(function() {
  angular.module('frontendAdmin').controller('CommentEditModal', ["$scope", "$uibModalInstance", "review", function($scope, $uibModalInstance, review) {
    console.log(review);
    $scope.review = review;
    $scope.closeModal = function() {
      return $uibModalInstance.dismiss();
    };
    $scope.submitIteraction = function() {
      return $uibModalInstance.close(review);
    };
  }]);

}).call(this);
