(function() {
  this.App.service('MnoeCurrentUser', ["$window", "$state", "$q", "$timeout", "MnoeApiSvc", function($window, $state, $q, $timeout, MnoeApiSvc) {
    var _self, userPromise;
    _self = this;
    userPromise = null;
    this.user = {};
    this.getUser = function() {
      if (userPromise != null) {
        return userPromise;
      }
      return userPromise = MnoeApiSvc.one('current_user').get().then(function(response) {
        angular.copy(response.data, _self.user);
        return response.data;
      });
    };
    this.skipIfNotAdmin = function() {
      var deferred;
      deferred = $q.defer();
      _self.getUser().then(function() {
        if ((_self.user.admin_role != null) && _self.user.admin_role === 'admin') {
          return deferred.resolve();
        } else {
          $timeout(function() {
            return $state.go('dashboard.home');
          });
          return deferred.reject();
        }
      });
      return deferred;
    };
    this.logout = function() {
      return _self.getUser().then(function(response) {
        if ((response.organizations != null) && response.organizations.length > 0) {
          return $window.location.assign("/dashboard/");
        } else {
          return $window.location.assign("/dashboard/#!/logout");
        }
      });
    };
    this.refreshUser = function() {
      userPromise = null;
      return _self.getUser().then(function() {
        return _self.skipIfNotAdmin();
      });
    };
    return this;
  }]);

}).call(this);
