(function() {
  this.App.service('RoutingHelper', ["$state", "$q", function($state, $q) {
    var _self;
    _self = this;
    this.skipUnlessCondition = function(condition) {
      if (condition) {
        return $q.resolve();
      } else {
        $timeout(function() {
          return $state.go('dashboard.home');
        });
        return $q.reject();
      }
    };
    return this;
  }]);

}).call(this);
