(function() {
  this.App.component('mnoeAuditEventsList', {
    templateUrl: 'app/components/mnoe-audit-events-list/mnoe-audit-events-list.html',
    bindings: {},
    controller: ["$log", "MnoeAuditEvents", function($log, MnoeAuditEvents) {
      var fetchEvents, updateSort, vm;
      vm = this;
      vm.events = {
        search: {},
        sort: "created_at.desc",
        nbItems: 20,
        offset: 0,
        page: 1,
        list: [],
        pageChangedCb: function(nbItems, page) {
          vm.events.nbItems = nbItems;
          vm.events.page = page;
          vm.events.offset = (page - 1) * nbItems;
          return fetchEvents(nbItems, vm.events.offset);
        }
      };
      vm.callServer = function(tableState) {
        vm.events.sort = updateSort(tableState.sort);
        return fetchEvents(vm.events.nbItems, vm.events.offset, vm.events.sort);
      };
      updateSort = function(sortState) {
        var sort;
        if (sortState == null) {
          sortState = {};
        }
        sort = "created_at.desc";
        if (sortState.predicate) {
          sort = sortState.predicate;
          if (sortState.reverse) {
            sort += ".desc";
          } else {
            sort += ".asc";
          }
        }
        return sort;
      };
      fetchEvents = function(limit, offset, sort) {
        if (sort == null) {
          sort = vm.events.sort;
        }
        vm.events.loading = true;
        return MnoeAuditEvents.list(limit, offset, sort).then(function(response) {
          vm.events.totalItems = response.headers('x-total-count');
          return vm.events.list = response.data;
        })["finally"](function() {
          return vm.events.loading = false;
        });
      };
    }]
  });

}).call(this);
