(function() {
  this.App.service('MnoeInvoices', ["MnoeAdminApiSvc", function(MnoeAdminApiSvc) {
    var _self;
    _self = this;
    this.currentBillingAmount = function() {
      return MnoeAdminApiSvc.all('invoices').customGET('current_billing_amount');
    };
    this.lastInvoicingAmount = function() {
      return MnoeAdminApiSvc.all('invoices').customGET('last_invoicing_amount');
    };
    this.outstandingAmount = function() {
      return MnoeAdminApiSvc.all('invoices').customGET('outstanding_amount');
    };
    this.lastPortfolioAmount = function() {
      return MnoeAdminApiSvc.all('invoices').customGET('last_portfolio_amount');
    };
    this.lastCommissionAmount = function() {
      return MnoeAdminApiSvc.all('invoices').customGET('last_commission_amount');
    };
    return this;
  }]);

}).call(this);
