(function() {
  this.App.controller('deleteDashboardTemplateCtrl', ["$scope", "$uibModalInstance", "dashboardTemplateId", "MnoeDashboardTemplates", "toastr", function($scope, $uibModalInstance, dashboardTemplateId, MnoeDashboardTemplates, toastr) {
    'ngInject';
    $scope.close = function() {
      return $uibModalInstance.close(false);
    };
    $scope["delete"] = function() {
      $scope.isLoading = true;
      return MnoeDashboardTemplates["delete"](dashboardTemplateId).then(function(success) {
        return toastr.success('mnoe_admin_panel.dashboard.dashboard_templates.widget.list.toastr.deleted.successfully');
      }, function(error) {
        return toastr.error('mnoe_admin_panel.dashboard.dashboard_templates.widget.list.toastr.deleted.error');
      })["finally"](function() {
        $scope.isLoading = false;
        return $uibModalInstance.close(true);
      });
    };
  }]);

}).call(this);
