(function() {
  this.App.controller('OrganizationController', ["$filter", "$stateParams", "$uibModal", "toastr", "MnoeOrganizations", "MnoeUsers", "MnoAppsInstances", function($filter, $stateParams, $uibModal, toastr, MnoeOrganizations, MnoeUsers, MnoAppsInstances) {
    'ngInject';
    var vm;
    vm = this;
    vm.orgId = $stateParams.orgId;
    vm.users = {};
    vm.hasDisconnectedApps = false;
    vm.status = {};
    vm.users.createUserModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/organization/create-user-modal/create-user.html',
        controller: 'CreateUserController',
        controllerAs: 'vm',
        resolve: {
          organization: vm.organization
        }
      });
      return modalInstance.result.then(function(user) {
        return vm.organization.members.push(user);
      });
    };
    MnoeOrganizations.get($stateParams.orgId).then(function(response) {
      vm.organization = response.data.plain();
      vm.organization.invoices = $filter('orderBy')(vm.organization.invoices, '-started_at');
      return vm.updateStatus();
    });
    vm.updateStatus = function() {
      vm.status = {};
      _.map(vm.organization.active_apps, function(app) {
        return vm.status[app.nid] = MnoAppsInstances.isConnected(app);
      });
      return vm.hasDisconnectedApps = false in _.countBy(vm.status);
    };
    vm.openAddAppModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/organization/add-app-modal/add-app-modal.html',
        controller: 'AddAppModalCtrl',
        controllerAs: 'vm',
        backdrop: 'static',
        windowClass: 'add-app-modal',
        size: 'lg',
        resolve: {
          organization: vm.organization
        }
      });
      return modalInstance.result.then(function(organization) {
        vm.organization = angular.copy(organization);
        return vm.updateStatus();
      });
    };
    vm.openRemoveAppModal = function(app, index) {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/organization/remove-app-modal/remove-app-modal.html',
        controller: 'RemoveAppModalCtrl',
        controllerAs: 'vm',
        backdrop: 'static',
        windowClass: 'remove-app-modal',
        size: 'md',
        resolve: {
          app: app
        }
      });
      return modalInstance.result.then(function(result) {
        if (result) {
          vm.organization.active_apps.splice(index, 1);
          return vm.updateStatus();
        }
      });
    };
    return vm;
  }]);

}).call(this);
