(function() {
  this.App.service('MnoAppsInstances', function() {
    var _self;
    _self = this;
    this.isConnected = function(instance) {
      return instance.stack === 'cube' || instance.stack === 'cloud' || _self.isOauthConnected(instance);
    };
    this.isOauthConnected = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    this.oAuthConnectPath = function(instance, extra_params) {
      var redirect;
      if (extra_params == null) {
        extra_params = '';
      }
      redirect = window.encodeURIComponent("" + location.pathname + location.hash);
      return "/mnoe/webhook/oauth/" + instance.uid + "/authorize?redirect_path=" + redirect + "&" + extra_params;
    };
    this.canBeDataSynced = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    this.dataSyncPath = function(instance) {
      return "/mnoe/webhook/oauth/" + instance.uid + "/sync";
    };
    this.canBeDisconnected = function(instance) {
      return instance.stack === 'connector' && instance.oauth_keys_valid;
    };
    this.disconnectPath = function(instance) {
      return "/mnoe/webhook/oauth/" + instance.uid + "/disconnect";
    };
    return this;
  });

}).call(this);
