(function() {
  this.App.controller('DashboardController', ["$scope", "$cookies", "$sce", "MnoeMarketplace", "MnoErrorsHandler", "MnoeCurrentUser", "MnoeAdminConfig", "STAFF_PAGE_AUTH", "REVIEWS_CONFIG", function($scope, $cookies, $sce, MnoeMarketplace, MnoErrorsHandler, MnoeCurrentUser, MnoeAdminConfig, STAFF_PAGE_AUTH, REVIEWS_CONFIG) {
    'ngInject';
    var main;
    main = this;
    main.errorHandler = MnoErrorsHandler;
    main.staffPageAuthorized = STAFF_PAGE_AUTH;
    main.isReviewingEnabled = REVIEWS_CONFIG && REVIEWS_CONFIG.enabled;
    main.isFinanceEnabled = MnoeAdminConfig.isFinanceEnabled();
    main.adminConfig = MnoeAdminConfig;
    main.isDashboardTemplatesEnabled = MnoeAdminConfig.isDashboardTemplatesEnabled();
    main.trustSrc = function(src) {
      return $sce.trustAsResourceUrl(src);
    };
    MnoeCurrentUser.getUser().then(main.user = MnoeCurrentUser.user);
    main.exit = function() {
      return MnoeCurrentUser.logout();
    };
  }]);

}).call(this);
