(function() {
  this.App.directive('mnoeOrganizationsList', ["$filter", "$log", "MnoeOrganizations", function($filter, $log, MnoeOrganizations) {
    return {
      restrict: 'E',
      scope: {
        list: '='
      },
      templateUrl: 'app/components/mnoe-organizations-list/mnoe-organizations-list.html',
      link: function(scope, elem, attrs) {
        var displayCurrentState, fetchOrganizations, setAllOrganizationsList, setLastOrganizationsList, setSearchOrganizationsList;
        scope.state = attrs.view;
        scope.organizations = {
          search: '',
          nbItems: 10,
          page: 1,
          pageChangedCb: function(nbItems, page) {
            var offset;
            scope.organizations.nbItems = nbItems;
            scope.organizations.page = page;
            offset = (page - 1) * nbItems;
            return fetchOrganizations(nbItems, offset);
          }
        };
        fetchOrganizations = function(limit, offset, sort) {
          if (sort == null) {
            sort = 'name';
          }
          scope.organizations.loading = true;
          return MnoeOrganizations.list(limit, offset, sort).then(function(response) {
            scope.organizations.totalItems = response.headers('x-total-count');
            return scope.organizations.list = response.data;
          }).then(function() {
            return scope.organizations.loading = false;
          });
        };
        scope.switchState = function() {
          scope.state = attrs.view = attrs.view === 'all' ? 'last' : 'all';
          return displayCurrentState();
        };
        displayCurrentState = function() {
          if (attrs.view === 'all') {
            setAllOrganizationsList();
            return fetchOrganizations(scope.organizations.nbItems, 0);
          } else if (attrs.view === 'last') {
            setLastOrganizationsList();
            return fetchOrganizations(scope.organizations.nbItems, 0, 'created_at.desc');
          } else {
            return $log.error('Value of attribute view can only be "all" or "last"');
          }
        };
        setAllOrganizationsList = function() {
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.list.all_organizations.title';
          return scope.organizations.switchLinkTitle = 'mnoe_admin_panel.dashboard.organization.widget.list.all_users.switch_link_title';
        };
        setLastOrganizationsList = function() {
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.list.last_organizations.title';
          return scope.organizations.switchLinkTitle = 'mnoe_admin_panel.dashboard.organization.widget.list.last_organizations.switch_link_title';
        };
        scope.searchChange = function() {
          if (scope.organizations.search.length >= 3) {
            scope.searchMode = true;
            return setSearchOrganizationsList(scope.organizations.search);
          } else if (scope.searchMode) {
            scope.searchMode = false;
            return displayCurrentState();
          }
        };
        setSearchOrganizationsList = function(search) {
          var terms;
          scope.organizations.loading = true;
          scope.organizations.widgetTitle = 'mnoe_admin_panel.dashboard.organization.widget.list.search_users.title';
          delete scope.organizations.switchLinkTitle;
          search = scope.organizations.search.toLowerCase();
          terms = {
            'name.like': "%" + search + "%"
          };
          return MnoeOrganizations.search(terms).then(function(response) {
            scope.organizations.totalItems = response.headers('x-total-count');
            return scope.organizations.list = $filter('orderBy')(response.data, 'name');
          })["finally"](function() {
            return scope.organizations.loading = false;
          });
        };
        return displayCurrentState();
      }
    };
  }]);

}).call(this);
