(function() {
  this.App.controller('UserController', ["$stateParams", "$window", "MnoeUsers", function($stateParams, $window, MnoeUsers) {
    'ngInject';
    var vm;
    vm = this;
    MnoeUsers.get($stateParams.userId).then(function(response) {
      return vm.user = response.data;
    });
    vm.impersonateUser = function() {
      var redirect, url;
      if (vm.user) {
        redirect = window.encodeURIComponent("" + location.pathname + location.hash);
        url = "/mnoe/impersonate/user/" + vm.user.id + "?redirect_path=" + redirect;
        return $window.location.href = url;
      }
    };
  }]);

}).call(this);
