(function() {
  this.App.service('MnoErrorsHandler', ["$log", function($log) {
    var _self, errorCache;
    _self = this;
    errorCache = null;
    this.processServerError = function(serverError, form) {
      var ref;
      $log.error('An error occurred:', serverError);
      if (_.startsWith(serverError.data, '<!DOCTYPE html>')) {
        return;
      }
      if ((400 <= (ref = serverError.status) && ref <= 499)) {
        errorCache = serverError;
        return _.each(errorCache.data, function(errors, key) {
          return _.each(errors, function() {
            if (form[key] != null) {
              return form[key].$setValidity('server', false);
            } else {
              return $log.error('MnoErrorsHandler: cannot find field:' + key);
            }
          });
        });
      }
    };
    this.errorMessage = function(name) {
      var result;
      result = [];
      if (errorCache != null) {
        _.each(errorCache.data[name], function(msg) {
          return result.push(msg);
        });
      }
      return result.join(', ');
    };
    this.resetErrors = function(form) {
      if (errorCache != null) {
        _.each(errorCache.data, function(errors, key) {
          return _.each(errors, function() {
            if (form[key] != null) {
              return form[key].$setValidity('server', null);
            }
          });
        });
        return errorCache = null;
      }
    };
    this.onlyServerError = function(form) {
      return _.every(form.$error, function(v, k) {
        return k === 'server';
      });
    };
    return this;
  }]);

}).call(this);
